import { Component, ViewChild, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Events } from 'ionic-angular';
import { Utils } from '../../../services/Utils';
import { AppService } from "../../../services/App.service";
import { NativeService } from "../../../services/NativeService"
import { BarcodeService, BarcodeType } from '../../../services/BarCodeService';
import { Api, ContenType, Method } from '../../../utils/appConstant';
@IonicPage()
@Component({
  selector: 'page-check-order',
  templateUrl: 'check-order.html',
})
export class CheckOrderPage {
  public result: any = {};
  public asnBillNo: string;
  public isPop: boolean;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public modalController: ModalController,
    public appService: AppService,
    public nativeService: NativeService,
    public barcodeService: BarcodeService,
    public events: Events,
    public ngZone: NgZone
  ) {
    this.asnBillNo = this.navParams.get('result') || '';
  }
  ionViewWillEnter() {
    if (!this.isPop) {
      this.queryDetails();
    } else {
      this.isPop = false;
    }
  }
  ionViewWillLeave() {

  }
  ionViewDidLoad() {

  }
  itemClick(item) {
    item['locCode'] = "STAGE";
    this.navCtrl.push('UpdateDetailPage', {
      result: item, callback: data => {
        this.isPop = data.isPop;
        if (data.result) {
          Object.assign(item, data.result);
        }
      }
    });
  }
  queryDetails() {
    this.nativeService.showLoading();
    this.appService.httpRequest(Api.Instock + Api.getAsnHeaderAndDetails, Method.Get, { asnBillNo: this.asnBillNo }, '', result => {
      this.nativeService.hideLoading();
      if (result.success) {
        this.result = result.data;
      } else {
        this.nativeService.showToast(result.msg);
      }
    });
  }
  backClick() {
    this.navCtrl.pop();
  }
}



